.btn {
  text-transform: uppercase;
  color: var(--white-color);
  cursor: pointer;
  background: linear-gradient(90.03deg, #00c5eb -1.97%, #b544fe 89.43%);
  border: none;
  border-radius: 5px;
  margin: auto auto 40px;
  padding: 11px 59px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.21;
  display: block;
}

@media screen and (min-width: 1280px) {
  .btn {
    margin-left: auto;
    margin-right: 0;
  }
}

.btn {
  position: relative;
  overflow: hidden;
}

.btn:after, .btn:before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn:before {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90deg, #00c5eb -2%, #b544fe 90%);
  background-repeat: no-repeat;
  background-size: cover;
}

.btn:after {
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  opacity: 0;
  background-color: #06071b;
  border-radius: 5px;
  transition: all .3s;
}

.btn__text {
  z-index: 10;
  position: relative;
}

.btn:hover:after, .btn:focus:after {
  opacity: 1;
}

