.footer__text {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: 0em;
  color: var(--white-color);
}

.footer {
  padding-top: 22px;
  padding-bottom: 22px;
  padding-right: 80px;
  border-top: 1px solid #ffffff;

  @media screen and (max-width: 480px) {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-image: url(../images/png-for-contact-and-footer/spr-mob-1x.png);
    background-position: top -317px left 68px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/png-for-contact-and-footer/spr-mob-1@2x.png);
      background-position: top -317px left 68px;
    }
  }
  @media screen and (min-width: 1280px) {
    padding-right: 0px;
  }
}
