.contact__us {
  font-size: 14px;

  @include section();
  color: var(--white-color);

  @media screen and (min-width: 768px) {
    @include section(74px, 80px);
  }

  &-titel {
    font-size: 20px;
    font-weight: 700;
    text-align: center;
    line-height: 1.2;
    margin-bottom: 40px;
    text-transform: uppercase;

    @media screen and (min-width: 768px) {
      font-size: 24px;
    }
  }
}

.contact__us-container {
  margin: 0 auto;
}

.contact__us {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: url(../images/png-for-contact-and-footer/spr-mob-1x.png),
    url(../images/png-for-contact-and-footer/spr-mob-2.png);
  background-position: top 300px left -328px, top -100px left 0px;

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url(../images/png-for-contact-and-footer/spr-mob-1@2x.png),
      url(../images/png-for-contact-and-footer/spr-mob-2@2x.png);
    background-position: top 300px left -328px, top -100px left 0px;
  }

  @media screen and (min-width: 480px) {
    background-image: url(../images/png-for-contact-and-footer/spr-tab-1.png);
    background-position: top -250px left 0;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/png-for-contact-and-footer/spr-tab-1@2x.png);
      background-position: top -250px left 0, top 1px left 1px;
    }
  }

  @media screen and(min-width:769px) {
    background-image: url(../images/png-for-contact-and-footer/spr-desk-1.png),
      url(../images/png-for-contact-and-footer/spr-tab-2.png);
    background-position: top -180px left -455px, top 1px left 1px;

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/png-for-contact-and-footer/spr-desk1@2x.png),
        url(../images/png-for-contact-and-footer/spr-tab-2@x.png);
      background-position: top -140px left -455px, top 149px left -50px;
    }

    @media screen and (min-width: 1280px) {
      background-position: top -430px left 0px;
    }
  }
}

.contact__us-items {
  @media screen and (min-width: 1280px) {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    flex-basis: calc((100% - 172px) / 2);
  }
}

.form {
  @media screen and (max-width: 767px) {
    display: flex;

    flex-direction: column;
  }

  @media screen and (min-width: 1280px) {
    width: 608px;
  }
}

.form__input {
  @media screen and (max-width: 767px) {
    display: flex;

    flex-direction: column;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    gap: 32px;
  }
}

.contact__us-label {
  display: flex;
  width: 100%;
  margin-bottom: 20px;
}

.contact__us-signecher {
  visibility: hidden;
  width: 0;
}

.subscribe_field {
  height: 40px;
  width: 100%;
  font-weight: 500;
  font-size: 14px;
  border-radius: 5px;
  padding-left: 8px;
  padding-right: 8px;
}

.subscribe_field:not(:placeholder-shown):required:valid {
  background-color: #ffffff;
}

.subscribe_field:not(:placeholder-shown):required:invalid {
  background-color: #ff1100;
}

.massage-fild {
  resize: none;
  height: 220px;
  width: 100%;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 12px;

  @media screen and(min-width:768px) {
    height: 168px;
  }
}

.titel {
  font-size: 20px;
  line-height: 1.21;
  text-align: center;
  color: var(--white-color);
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    font-size: 24px;
  }

  @media screen and (min-width: 1280px) {
    text-align: left;
  }
}

.description {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.21;
  text-align: center;
  margin-bottom: 40px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }

  @media screen and (min-width: 1280px) {
    text-align: left;
    max-width: 500px;
  }
}

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-bottom: 42px;

  @media screen and (min-width: 1280px) {
    justify-content: start;
  }

  &__item {
    width: 40px;
    height: 40px;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-image: linear-gradient(90deg, #00c5eb -2%, #b544fe 90%);
  }
}

.icon-regular {
  .social__link:hover .social__icon & {
    opacity: 1;
  }
}

.icon-hovered {
  opacity: 0;
  transition-duration: 400ms;
  .social__link:hover .social__icon & {
    opacity: 1;
  }
}

.contacts {
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 12px;

  @media screen and (min-width: 1280px) {
    text-align: left;
  }

  &__link {
    color: var(--white-color);
    font-size: 14px;
    line-height: 1.21;
  }
}
